<template>
    <div>
        <top-nav title="积分商品列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1） 这里是积分商品列表</p>

        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item  label="商品名称" label-width="100">
                        <el-input v-model="searchForm.goods_name"></el-input>
                    </el-form-item>

                    <el-form-item  label="商品编号" label-width="100">
                        <el-input v-model="searchForm.goods_sn" ></el-input>
                    </el-form-item>

                    <el-form-item  label="是否包邮" label-width="100">
                        <el-select v-model="searchForm.is_free_shipping" placeholder="请选择">
                            <el-option :key="-999" label="请选择" :value="-999"></el-option>
                            <el-option :key="0" label="不包邮" :value="0"></el-option>
                            <el-option :key="1" label="包邮" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item  label="是否推荐" label-width="100">
                        <el-select v-model="searchForm.is_recommend" placeholder="请选择">
                            <el-option :key="-999" label="请选择" :value="-999"></el-option>
                            <el-option :key="0" label="不推荐" :value="0"></el-option>
                            <el-option :key="1" label="推荐" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                        <el-button @click="searchForm.goods_name='',searchForm.goods_sn='',searchForm.is_free_shipping=-999,
                        searchForm.is_recommend=-999,getList(1)" size="mini" type="text">取消搜索</el-button>
                        <router-link to="/marketing/integral/create">
                            <el-button size="mini" type="danger">新增商品</el-button>
                        </router-link>
                    </div>
                </el-form>

                <el-table :data="datas.list" style="width: 100%" size="small">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="goods_id" label="编码/ID" width="200">
                        <template slot-scope="scope">
                            <p>系统ID：<span>{{scope.row.goods_id}}</span></p>
                            <p>商品编码：<span>{{scope.row.goods_sn}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品名称" width="240">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.goods_name}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="缩略图" width="80">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.photo"/>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品价格" width="260">
                        <template slot-scope="scope">
                            <p>市场价：<span style="color: red">￥{{scope.row.market_price}}</span></p>
                            <p>兑换价：
                                <span v-if="scope.row.shop_price > 0" style="color: red">￥{{scope.row.shop_price}}</span>
                                <span v-if="scope.row.integral > 0" style="color: red"> + {{scope.row.integral}} 积分</span>
                            </p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="分类" width="100">
                        <template slot-scope="scope">
                            <span class="ellipsis-01">{{scope.row.cat_id}}</span>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="扩展信息" width="150" >
                        <template slot-scope="scope">
                            <el-row>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_free_shipping == 1" size="mini">包邮</el-tag>
                                    <el-tag v-if="scope.row.is_free_shipping == 0" type="info" size="mini">包邮</el-tag>
                                </el-col>

                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_recommend == 1" size="mini">推荐</el-tag>
                                    <el-tag v-if="scope.row.is_recommend == 0" type="info" size="mini">推荐</el-tag>
                                </el-col>
                            </el-row>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="浏/评/销/排" width="200">
                        <template slot-scope="scope">
                            <el-row>
                                <el-col :span="12"><p>浏览：<span>{{scope.row.views}}</span></p></el-col>
                                <el-col :span="12"><p>排序：<span>{{scope.row.orderby}}</span></p></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="12"><p>评论：<span>{{scope.row.comment_count}}</span></p></el-col>
                                <el-col :span="12"><p>销量：<span>{{scope.row.sales_sum}}</span></p></el-col>
                            </el-row>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作日期" width="200">
                        <template slot-scope="scope">
                            <p>添加：{{scope.row.add_time}}</p>
                            <p>修改：{{scope.row.last_update}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="上/下架" width="80">
                        <template slot-scope="scope">
                            <el-switch :value="scope.row.is_online" :inactive-value="0" :active-value="1" @change="setOnline(scope.row)"></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <router-link :to="'/marketing/integral/edit/' + scope.row.goods_id">
                                <el-button size="mini" type="text">编辑</el-button>
                            </router-link>&nbsp;
                            <el-popover placement="top" v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="del(scope)">确定</el-button>
                                </div>
                                <el-button type="text" size="mini" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>

                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: {
                    goods_name: '',
                    goods_sn: '',
                    is_free_shipping: -999,
                    is_recommend: -999,
                },
            }
        },
        created() {
            this.getList();
        },
        methods: {

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.integral.goods/index", {
                    appToken: this.$store.getters.appToken,
                    goods_name: this.searchForm.goods_name,
                    goods_sn: this.searchForm.goods_sn,
                    is_free_shipping: this.searchForm.is_free_shipping,
                    is_recommend: this.searchForm.is_recommend,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            setOnline(row){
                this.loading = true;
                api.post("/shop/admin.integral.goods/online", {
                    appToken: this.$store.getters.appToken,
                    goods_id : row.goods_id,
                }).then(res => {
                    this.loading = false;
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del(scope) {
                this.loading = true;
                api.post("/shop/admin.integral.goods/del", {
                    appToken: this.$store.getters.appToken,
                    goods_id : scope.row.goods_id,
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    scope.row.is_delete = false;
                    this.loading = false;
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            }

        }
    }
</script>

